import React, { Component } from 'react'
import './App.scss'
import { Route, Link } from 'react-router-dom'

import AuthenticatedRoute from './auth/components/AuthenticatedRoute'
import Header from './header/Header'
import SignUp from './auth/components/signup/SignUp'
import SignIn from './auth/components/signin/SignIn'
import SignOut from './auth/components/SignOut'
import ChangePassword from './auth/components/changepassword/ChangePassword'
import IndexMountains from './mountains/components/AllMountains/IndexMountains'
import ShowMountain from './mountains/components/AllMountains/ShowMountain'
import ShowMyMountain from './mountains/components/UserCompletedSummits/ShowMyMountain'
import MyCompletedSummits from './mountains/components/UserCompletedSummits/MyCompletedSummits'
import Home from './home/Home'

class App extends Component {
  constructor () {
    super()

    this.state = {
      user: null,
      flashMessage: '',
      flashType: null
    }
  }

  setUser = user => this.setState({ user })

  clearUser = () => this.setState({ user: null })

  flash = (message, type) => {
    this.setState({ flashMessage: message, flashType: type })

    clearTimeout(this.messageTimeout)

    this.messageTimeout = setTimeout(() => this.setState({flashMessage: null
    }), 3000)
  }

  render () {
    const { flashMessage, flashType, user } = this.state

    return (
      <React.Fragment>
        <Header user={user} />
        {flashMessage && <h3 className={flashType}>{flashMessage}</h3>}

        <main className="body">
          <Route exact path='/' render={() => (
            <Home />
          )} />
          <Route path='/sign-up' render={() => (
            <SignUp flash={this.flash} setUser={this.setUser} />
          )} />
          <Route path='/sign-in' render={() => (
            <SignIn flash={this.flash} setUser={this.setUser} />
          )} />
          <AuthenticatedRoute user={user} path='/sign-out' render={() => (
            <SignOut flash={this.flash} clearUser={this.clearUser} user={user} />
          )} />
          <AuthenticatedRoute user={user} path='/change-password' render={() => (
            <ChangePassword flash={this.flash} user={user} />
          )} />
          <AuthenticatedRoute user={user} exact path='/mountains/:id/show' render={(props) => (
            <ShowMountain flash={this.flash} user={user} id={props.match.params.id}/>
          )} />
          <AuthenticatedRoute user={user} exact path='/mountains/:id/show-completed-summit' render={(props) => (
            <ShowMyMountain flash={this.flash} user={user} id={props.match.params.id}/>
          )} />
          <AuthenticatedRoute user={user} exact path='/completed-summits' render={() => (
            <MyCompletedSummits flash={this.flash} user={user}/>
          )} />
          <AuthenticatedRoute user={user} exact path='/mountains' render={() => (
            <IndexMountains flash={this.flash} user={user}/>
          )} />
        </main>
      </React.Fragment>
    )
  }
}

export default App



